import React from 'react'
import { SvgBottom } from './svg/SvgBottom'
import { SvgTop } from './svg/SvgTop'

export const Leasing = () => {
  return (
    <section className="leasing_main" id="leasing">
      <div className="leasing_container">
        
        <SvgTop/>

        <article className="caja_blanca">

          <div className="leasing_text">
            <h1>Conoce el Leasing <span>Davivienda</span></h1>
          </div>

          <div className="leasing_content">

            <div className="leasing_paragraf">
              <p>Diferente a cualquier otro, <span>hecho a la medida</span> . Así es el Leasing Davivienda: una alternativa para aprovechar lo que mes a mes pagas en arriendo.</p>

              <p>Para todos los bolsillos, para quien compra su casa por primera vez o para quien quiere invertir...</p>

              <p> <span>¡El Leasing Davivienda es para todos!</span> </p>
            </div>

            <div className="leasing_list">
              <ul>
                <li>Financia hasta el 80% del inmueble </li>
                <li>Aplica para VIS y No VIS</li>
                <li>Cuotas e intereses más bajos</li>
              </ul>
            </div>
            
          </div>

        </article>
        <SvgBottom/>
      </div>
    </section>
  )
}
