import React from 'react'
import { SvgBottom } from './svg/SvgBottom'
import { SvgTop } from './svg/SvgTop';

export const Viviendanovis = () => {
  return (
    <section className="viviendanovis_main" id="vivienda-novis">
      <div>
        
        <SvgTop/>

        <article className="caja_blanca">

          <div className="viviendanovis_imgs">
            <figure className="imgs_content">
              <img src="./assets/img/mg2.webp" alt="Invertir en Colombia desde el exterior mediante leasing" />
            </figure>
          </div>

          <div className="viviendanovis_text">

            <div className="viviendanovis_content">

              <div className="viviendanovis_title">
                <h1>
                  Invertir en Colombia desde <span>el exterior</span>
                </h1>
                <h2>Leasing Davivienda para vivienda No VIS</h2>
              </div>

              <div className="viviendanovis_paragraf">
                <p>
                  El Leasing Davivienda es una alternativa para todos los bolsillos, para quien compra por primera vez o para quien quiere invertir.
                </p>
                <p>
                  Financia tu casa con cuotas más bajas a través de un contrato de arrendamiento en el que cancelas un arriendo mensual (cánones) y al finalizar el tiempo pactado, te haces propietario del inmueble.
                </p>
              </div>

              <div className="viviendanovis_list">
                <ul>
                  <li>
                    <button className='widget'>Hablar con <span>un asesor</span> </button>
                  </li>
                  <li>
                      <button  onClick={ () => {window.open("https://colraices.com/proyectosdeviviendaencolombiadesdeelexterior",'_blank')}}>Búsqueda de <span>inmueble No VIS</span> </button>
                  </li>
                </ul>
              </div>
              
            </div>
          </div>

        </article>
        <SvgBottom/>
      </div>
    </section>
  )
}
